.page-header {
  width: 100%;
  color: #ffffff;
  background-color: #282828;
  background-image: url("../img/intro.jpg");
  background-size: 1440px 825px;
  background-repeat: no-repeat;
  background-position: 50%; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .page-header {
      background-image: url("../img/intro@2x.jpg"); } }
  @media (max-width: 1023px) {
    .page-header {
      position: relative;
      background: none; } }

.page-header__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-bottom: 220px;
  width: 880px;
  margin: 0 auto; }
  @media (max-width: 1023px) {
    .page-header__wrapper {
      width: 100%;
      display: block; } }

.page-header__intro {
  width: 395px; }
  @media (max-width: 1023px) {
    .page-header__intro {
      padding: 134px 18.5% 80px;
      width: 100%;
      text-align: center;
      background-color: #282828;
      background-image: url("../img/intro-tablet.jpg");
      background-size: cover;
      background-repeat: no-repeat; } }
  @media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
    .page-header__intro {
      background-image: url("../img/intro-tablet@2x.jpg"); } }
  @media (max-width: 767px) {
    .page-header__intro {
      padding: 120px 5% 70px;
      background-image: url("../img/intro-mob.jpg");
      background-position: 50%; } }
  @media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
    .page-header__intro {
      background-image: url("../img/intro-mob@2x.jpg"); } }

.page-header__form {
  width: 327px; }
  @media (max-width: 1023px) {
    .page-header__form {
      width: 100%; } }

@media (max-width: 1023px) {
  .page-header__logo {
    position: absolute;
    top: 25px;
    left: 3%; } }

@media (max-width: 1023px) {
  .page-header__logo {
    position: absolute;
    top: 25px;
    left: 3%; } }

@media (max-width: 767px) {
  .page-header__logo {
    left: 5%; } }

.main-nav {
  margin-bottom: 255px;
  width: 100%; }
  @media (max-width: 1023px) {
    .main-nav {
      display: none; } }
  @media (max-width: 1023px) {
    .main-nav {
      display: block;
      margin-bottom: 0;
      width: 100%; } }

@media (max-width: 1023px) {
  .page-header--closed-menu .main-nav {
    display: none; } }

.site-list {
  display: flex;
  padding: 21px 25px 9px 22px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2); }
  @media (max-width: 1023px) {
    .site-list {
      padding-top: 55px;
      display: block;
      background-color: #282828;
      border: none; } }

.site-list__item {
  flex-grow: 1;
  padding-right: 10px;
  font-weight: 300;
  opacity: 0.7; }
  .site-list__item:last-of-type {
    flex-grow: 0;
    padding-right: 0; }
  .site-list__item:hover {
    opacity: 1; }
  .site-list__item:active {
    color: #0ad9c6;
    opacity: 0.7; }
  @media (max-width: 1023px) {
    .site-list__item {
      font-weight: 400;
      font-size: 20px;
      line-height: 28px; } }

@media (max-width: 1023px) {
  .page-header--opened-menu .site-list {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 3;
    width: 100%;
    padding: 100px 0;
    background-color: #ffffff; } }

@media (max-width: 1023px) {
  .page-header--opened-menu .site-list__item {
    padding: 16px 5%;
    margin-bottom: 30px;
    color: #48494d;
    opacity: 1;
    background-color: #f6f6f6; } }

.intro__item--title {
  margin: 0;
  margin-top: 19px;
  font-weight: 700;
  font-size: 46px;
  letter-spacing: -0.23px;
  line-height: 64px; }
  @media (max-width: 1023px) {
    .intro__item--title {
      margin-top: 0;
      font-size: 30px;
      letter-spacing: -0.15px;
      line-height: 42px; } }

.intro__item--text {
  margin: 0;
  margin-top: 24px;
  font-weight: 300;
  font-size: 20px;
  letter-spacing: -0.1px;
  opacity: 0.8; }
  @media (max-width: 1023px) {
    .intro__item--text {
      margin-top: 20px;
      font-size: 18px; } }

.form {
  padding: 4px 0 20px; }
  @media (max-width: 1023px) {
    .form {
      padding: 70px 28% 40px;
      text-align: center; } }
  @media (max-width: 767px) {
    .form {
      padding-left: 5%;
      padding-right: 5%; } }

.form__text {
  margin: 0;
  text-align: center;
  font-size: 17px;
  line-height: 28px; }
  @media (max-width: 1023px) {
    .form__text {
      margin-bottom: 30px;
      font-size: 18px;
      color: #000000; } }
  @media (max-width: 767px) {
    .form__text {
      margin-bottom: 20px; } }

.form__item {
  width: 100%;
  margin-top: 20px; }
  @media (max-width: 1023px) {
    .form__item {
      margin: 0 auto 20px;
      width: 95%; } }
  @media (max-width: 767px) {
    .form__item {
      width: 100%; } }

.form__input {
  padding: 14px 10px 8px 30px;
  font-size: 15px;
  line-height: 24px;
  color: #1f1f1f;
  border-radius: 4px;
  border: solid 1px #c9ccd4; }
  .form__input:hover, .form__input:active, .form__input:focus {
    box-shadow: 0 0 0 2px #0ad9c6;
    outline: none; }
  @media (max-width: 1023px) {
    .form__input {
      padding-left: 21px; } }
  @media (max-width: 767px) {
    .form__input {
      padding-left: 26px; } }

.form__input::placeholder {
  color: #1f1f1f;
  opacity: 0.5; }

.form__button {
  display: flex;
  justify-content: center;
  padding: 15px;
  font-weight: 600;
  font-size: 15px;
  line-height: 21px;
  color: #000000;
  background-color: #0ad9c6;
  border-width: 0;
  border-radius: 6px;
  cursor: pointer; }
  .form__button:hover, .form__button:active {
    background-color: #70ecdf; }
  .form__button:focus {
    background-color: #70ecdf;
    box-shadow: 0 0 0 3px #038d7e;
    outline: none; }

.toggle {
  display: none;
  position: absolute;
  right: 0;
  top: 0;
  width: 50px;
  height: 50px;
  padding: 0;
  border: none; }

@media (max-width: 1023px) {
  .page-header--no-js .toggle {
    display: none; } }

@media (max-width: 1023px) {
  .page-header--closed-menu .toggle {
    display: block;
    background-color: #0ad9c6; } }

@media (max-width: 1023px) {
  .page-header--opened-menu .toggle {
    display: block;
    z-index: 5;
    background-color: transparent; } }

.page-header--closed-menu .toggle__icon--burger,
.page-header--opened-menu .toggle__icon--cross {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.page-header--opened-menu .toggle__icon--burger,
.page-header--closed-menu .toggle__icon--cross {
  display: none; }
